<template>
  <div class="home">
    <!-- 这里是搜索头部 -->
    <div class="inputCont">
      <div class="input">
        <input
          type="text"
          name="inputCont"
          v-model="inputValue"
          placeholder="请输入运单号码"
        />
        <i class="close" @click="close"></i>
      </div>
      <div class="search" @click="search"><span></span></div>
    </div>

    <!-- 这是内容部分 -->
    <div class="content">
      <div class="oldList">
        <h2>历史搜索结果</h2>
        <transition name="fade">
          <ul class="listCont" v-show="show">
            <li v-for="(item, i) in list" :key="i" @click="liItem(item)">
              <p>{{ item.num }}</p>
              <span class="closeBtn" @click.stop="closeBtn(i)"><i></i></span>
            </li>
          </ul>
        </transition>
        <span @click="openBtn"><i></i></span>
      </div>

      <div class="dataCont" v-if="listItemCont.length>0">
        <div class="header">
          <div class="time">
            <div class="text">时间</div>
            <div class="icon" @click="sort=!sort">
              <div class="topT" :class="{active:sort}"></div>
              <div class="bottomT" :class="{active:!sort}"></div>
            </div>
          </div>
          <div class="addr">地点和跟踪进度</div>
        </div>

        <div class="dataList" v-for="(item,i) in showList" :key="i">
          <div class="listItem">
            <div class="listYear">{{item.time.split(' ')[0]}}</div>
            <div class="listHour">{{item.time.split(' ')[1]}}</div>
          </div>
          <div class="midStyle">
            <div class="circle" :class="{active:(sort&&i==0)||((!sort)&&i==(showList.length-1))}"></div>
            <div class="line" :class="{active:(sort&&i==showList.length-1)||((!sort)&&i==showList.length-1)}"></div> 
          </div>
          <div class="addrText">
            {{item.status}}
          </div>
        </div>
      </div>
      <div v-else>
        <h1>今日新闻</h1>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Home",
  data: function () {
    return {
      inputValue: "773099340764407",
      list: [],
      listItemCont:[],
      show: false,
      sort:true
    };
  },
  computed:{
    showList(){
      if(this.sort == true){
        return [...this.listItemCont]
      }else{
        return [...this.listItemCont].reverse()
      }
    }
  },
  methods: {
    // 清空输入框的内容
    close: function () {
      console.log(this);
      this.inputValue = "";
    },
    // 异步搜索
    async search(number) {
      number = this.inputValue;
      // 如果输入框为空值则不能写入，并返回出去
      if (this.inputValue == "") {
        return;
      } else {
        this.list.push({
          num: number,
        });
      }
      // 存储数据
      localStorage.setItem("list", JSON.stringify(this.list));
      let httpUrl = `https://qyexpress.market.alicloudapi.com/composite/queryexpress?number=${number}`
      let config = {headers: {'Authorization': "APPCODE a11e71e78a4745dea09fad12a0b8ac9a",},};
      let res = await axios.get(httpUrl, config);
      console.log(res);
      if(res.data.resp.RespCode == '0'){
        this.listItemCont = res.data.data.list
      }else{
        console.error('輸入的快递单号错误')
      }
      
      // console.log(this.listItemCont.length)
    },
    // 展开历史记录按钮
    openBtn: function () {
      let opBtn = document.querySelector(".oldList>span");
      this.show = !this.show;
      if (this.show == true) {
        opBtn.style.cssText =
          "transform: rotate(-180deg);transition: transform 1s;";
      } else {
        opBtn.style.cssText =
          "transform: rotate(0deg);transition: transform 1s;";
      }
    },
    // 删除历史数据按钮
    closeBtn(key) {
      console.log(this.list[key].num);
      // confirm("是否移除"+this.list[key].num+"?");
      this.list.splice(key, 1);
      localStorage.setItem("list", JSON.stringify(this.list));
    },
    liItem:function(item){
      console.log(item)
      this.inputValue = item.num
      this.search()
      this.openBtn()
    }
  },
  mounted() {
    // json字符串转化为json对象
    var list = JSON.parse(localStorage.getItem("list"));
    // 判断list是否存在
    if (list) {
      // 存在则将获取到的list数据保存到list中
      this.list = list;
      console.log(this.list);
    }
  },
};
</script>

